// components
import { SelectLang } from "../SelectLang/SelectLang";

//mui-components
import { Box, Button, Tooltip } from "@mui/material";

// mui-icons
import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice';
import DeleteSweepIcon from '@mui/icons-material/DeleteSweep';
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';
import AccessTimeFilledRoundedIcon from '@mui/icons-material/AccessTimeFilledRounded';

// constants
import { BTN_COLOR, BTN_HOVER_COLOR, LIGHT_BLACK, SPEECH_BUBBLE_DARK_SIDE, TEXT_TO_SPEECH } from "../../constants/constants";

// mui-styled
import styled from "@emotion/styled";

const DictaphoneBox = styled(Box)({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  flexDirection: 'row',
  flexWrap: 'wrap',
  margin: '10px',
});

const DictaphontBtn = styled(Button)({
  margin: '15px',
  width: '64px',
  height: '64px',
  borderRadius: '40px',
  backgroundColor: BTN_COLOR,
  ':hover': {
    backgroundColor: BTN_HOVER_COLOR,
  },
  '@media screen and (max-width: 1024px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
  '@media screen and (max-height: 750px) and (min-width: 1025px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
});

const ResetInputButton = styled(Button)({
  margin: '15px',
  width: '64px',
  height: '64px',
  borderRadius: '40px',
  backgroundColor: BTN_COLOR,
  ':hover': {
    backgroundColor: BTN_HOVER_COLOR,
  },
  '@media screen and (max-width: 1024px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
  '@media screen and (max-height: 750px) and (min-width: 1025px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
});

const SetModeBtn = styled(Button)({
  margin: '5px',
  width: '64px',
  height: '64px',
  borderRadius: '40px',
  backgroundColor: BTN_COLOR,
  ':hover': {
    backgroundColor: BTN_HOVER_COLOR,
  },
  '@media screen and (max-width: 1024px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
  '@media screen and (max-height: 750px) and (min-width: 1025px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
});

const InterimResultsBtn = styled(Button)({
  margin: '5px',
  width: '64px',
  height: '64px',
  borderRadius: '40px',
  backgroundColor: BTN_COLOR,
  ':hover': {
    backgroundColor: BTN_HOVER_COLOR,
  },
  '@media screen and (max-width: 1024px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
  '@media screen and (max-height: 750px) and (min-width: 1025px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
});

const iconStyles = {
  fontSize: '32px',
  color: SPEECH_BUBBLE_DARK_SIDE,
  '@media screen and (max-width: 1024px)': {
    fontSize: '24px'
  },
  '@media screen and (max-height: 750px) and (min-width: 1025px)': {
    fontSize: '24px',
  }
};

export const SpeechToTextUtils = ({listening, stop, setValue, listen, lang, setLang, setMode, interimResults, setInterimResults}) => {

    const onDictaphoneBtnClicked = () => {
      if (listening) {
        
        try {
          stop();
        } catch {}

      } else {

        try {
          listen({ interimResults, lang: lang });
        } catch {}

      }
    };

    const onSetModeBtnClicked = () => {
      const app = document.querySelector('.App');
      
      app.style.transform = 'rotateY(90deg)';

      setTimeout(() => setMode(TEXT_TO_SPEECH), 210);
      setTimeout(() => app.style.transform = 'rotateY(0deg)', 230);
    };

    const onResetBtnClicked = () => setValue('');

    const dictaphoneBtnAnimation = {
      animation: listening ? 'microphone .5s ease-in infinite' : '',
      '@keyframes microphone': {
        "0%": {
          transform: 'scale(1)',
        },
        "50%": {
          transform: 'scale(1.1)',
        },
        '100%': {
          transform: 'scale(1)',
        }
      },
    };

    const interimBtnStyles = {
      transition: 'color 150ms ease',
      color: interimResults ? SPEECH_BUBBLE_DARK_SIDE : LIGHT_BLACK,
    }

    const onInterimBtnClicked = () => {
      stop();
      setInterimResults(!interimResults);
    };

    return <DictaphoneBox>
        <SelectLang lang={lang} setLang={setLang} stop={stop} />
        <Tooltip title='Speech'>
            <DictaphontBtn sx={dictaphoneBtnAnimation} onClick={onDictaphoneBtnClicked} variant='contained'>
                <KeyboardVoiceIcon sx={iconStyles} />
            </DictaphontBtn>
        </Tooltip>
        <Tooltip title='Interim results'>
            <InterimResultsBtn onClick={onInterimBtnClicked} variant='contained'>
                <AccessTimeFilledRoundedIcon sx={{...iconStyles, ...interimBtnStyles}} />
            </InterimResultsBtn>
        </Tooltip>
        <Tooltip title='Reset'>
            <ResetInputButton onClick={onResetBtnClicked} variant='contained'>
                <DeleteSweepIcon sx={iconStyles} />
            </ResetInputButton>
        </Tooltip>
        <Tooltip title={TEXT_TO_SPEECH}>
          <SetModeBtn onClick={onSetModeBtnClicked}>
            <CompareArrowsIcon sx={iconStyles} />
          </SetModeBtn>
        </Tooltip>
    </DictaphoneBox>
    
};