// react
import { useState } from "react";

// components
import { SpeechToTextUtils } from "../SpeechToTextUtils/SpeechToTextUtils";

// mui-components & hooks
import { Box, TextField, Typography, useMediaQuery } from "@mui/material";

// hooks
import { useSpeechRecognition } from "react-speech-kit";

// constants
import { SPEECH_BUBBLE_DARK_SIDE, SPEECH_BUBBLE_LIGHT_SIDE } from "../../constants/constants";

// mui-styled
import styled from "@emotion/styled";

const SpeechToTextBox = styled(Box)({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  flexDirection: 'column',
});

const TranscriptDescription = styled(Typography)({
  margin: '15px 0',
  color: SPEECH_BUBBLE_DARK_SIDE,
  fontSize: '18px',
  padding: '0 10px',
  textAlign: 'center',
  '@media screen and (max-width: 1024px)': {
    fontSize: '16px'
  },
  '@media screen and (max-width: 768px)': {
    fontSize: '14px'
  },
  '@media screen and (max-height: 750px) and (min-width: 769px)': {
    fontSize: '14px'
  },
});

export const SpeechToText = ({lang, setLang, setMode}) => {

  const [value, setValue] = useState('');
  const [isFocused, setIsFocused] = useState(false);
  const [interimResults, setInterimResults] = useState(true);

  const { listen, listening, stop, supported } = useSpeechRecognition({
    onResult: result => {
      if (interimResults) {
        setValue(result);
      } else {
        setValue(prev => prev + ' ' + result);
      }
    }
  });
  
    const onFieldChange = e => setValue(e.target.value);

    const onFocus = () => setIsFocused(true);
    const onBlur = () => setIsFocused(false);

    const is1024px = useMediaQuery('@media screen and (max-width: 1024px)');
    const is768px = useMediaQuery('@media screen and (max-width: 768px)');
    const is750pxHeight = useMediaQuery('@media screen and (max-height: 750px) and (min-width: 769px)');

    const inputProps = {
      readOnly: true,
      inputProps: {
        style: {
          color: SPEECH_BUBBLE_LIGHT_SIDE,
          fontFamily: `"Nunito", "roboto", "sans-serif`,
          width: '60vw',
          textAlign: 'center',
          overflow: 'auto',
          maxHeight: '30vh',
          fontSize: is768px ? '16px' : is1024px ? '18px' : is750pxHeight ? '16px' : '20px',
        }
      }
    };

    if (supported) {
      return <SpeechToTextBox>
      <SpeechToTextUtils listening={listening} setValue={setValue} stop={stop} listen={listen} lang={lang} setLang={setLang} setMode={setMode} 
      setInterimResults={setInterimResults} interimResults={interimResults} />
      <TranscriptDescription variant='h6' component='p'>{listening ? 'Listening you...' : 'Click the microphone icon and start talking to see the transcript.'}</TranscriptDescription>
      <TextField
        multiline
        autoFocus={isFocused}
        InputProps={inputProps}
        value={value}
        placeholder="Here you will see the output of what you are saying. Take some pause to get the result."
        onChange={onFieldChange}
        onFocus={onFocus}
        onBlur={onBlur}
      />
    </SpeechToTextBox>
    }
};