// react
import { useState, useEffect } from "react";

// components
import { TextToSpeechUtils } from "../TextToSpeechUtils/TextToSpeechUtils";
import { Sliders } from "../Sliders/Sliders";

// mui-components & hooks
import { Box, TextField, useMediaQuery } from "@mui/material";

// hooks
import { useSpeechSynthesis } from 'react-speech-kit';

// constants
import { SPEECH_BUBBLE_LIGHT_SIDE } from "../../constants/constants";

// mui-styled
import styled from "@emotion/styled";

const TextToSpeechBox = styled(Box)({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  flexDirection: 'column',
});

export const TextToSpeech = ({setMode}) => {

    const [value, setValue] = useState('');
    const [voice, setVoice] = useState('');
    const [isFocused, setIsFocused] = useState(false);

    const [rate, setRate] = useState(1);
    const [pitch, setPitch] = useState(1);

    const { speak, speaking, voices, cancel, supported } = useSpeechSynthesis();

    useEffect(() => {
      if (voices.length) setVoice(voices[0].name);
    }, [voices.length]);

    const onFieldChange = e => setValue(e.target.value);
    
    const onFocus = () => setIsFocused(true);
    const onBlur = () => setIsFocused(false);

    const is1024px = useMediaQuery('@media screen and (max-width: 1024px)');
    const is768px = useMediaQuery('@media screen and (max-width: 768px)');
    const is750pxHeight = useMediaQuery('@media screen and (max-height: 750px) and (min-width: 769px')

    const inputProps = {
      readOnly: false,
      inputProps: {
        style: {
          color: SPEECH_BUBBLE_LIGHT_SIDE,
          fontFamily: `"Nunito", "roboto", "sans-serif`,
          width: '60vw',
          textAlign: 'center',
          overflow: 'auto',
          maxHeight: '30vh',
          fontSize: is768px ? '16px' : is1024px ? '18px' : is750pxHeight ? '16px' : '20px',
        }
      }
    };

      if (supported) {
        return <TextToSpeechBox>
        <TextToSpeechUtils setMode={setMode} speaking={speaking} speak={speak} value={value} voices={voices} 
        voice={voice} setVoice={setVoice} cancel={cancel} rate={rate} pitch={pitch} />
        <Sliders rate={rate} pitch={pitch} setRate={setRate} setPitch={setPitch} />
        <TextField
          multiline
          autoFocus={isFocused}
          InputProps={inputProps}
          defaultValue={value}
          placeholder="Write here a text you want to listen"
          onChange={onFieldChange}
          onFocus={onFocus}
          onBlur={onBlur}
        />
      </TextToSpeechBox>
      }
};