// mui-components
import { Box, Slider, Typography } from "@mui/material";

// mui-styled
import styled from "@emotion/styled";

const StyledSlidersMainBox = styled(Box)({
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    flexWrap: 'wrap',
});

const SliderBox = styled(Box)({
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    margin: '5px 20px 10px',
    width: '200px',
});

const SliderTitle = styled(Typography)({
    fontSize: '18px',
    '@media screen and (max-width: 1024px)': {
        fontSize: '16px'
    },
    '@media screen and (max-width: 768px)': {
        fontSize: '14px'
    },
    '@media screen and (max-height: 750px) and (min-width: 769px)': {
        fontSize: '14px'
    },
});

export const Sliders = ({rate, pitch, setRate, setPitch}) => {

    const onPitchChange = e => setPitch(e.target.value);
    const onRateChange = e => setRate(e.target.value);

    return  <StyledSlidersMainBox>
        <SliderBox>
            <SliderTitle variant="h5" component='h6'>Rate</SliderTitle>
            <Slider
                size="medium"
                value={rate}
                valueLabelDisplay="auto"
                min={0.1}
                step={0.01}
                max={3}
                onChange={onRateChange}
            />
        </SliderBox>
        <SliderBox>
            <SliderTitle variant="h5" component='h6'>Pitch</SliderTitle>
            <Slider
                size="medium"
                value={pitch}
                valueLabelDisplay="auto"
                min={0}
                step={0.01}
                max={2}
                onChange={onPitchChange}
            />
        </SliderBox>
  </StyledSlidersMainBox>
};