// react
import { useState } from "react";

// components
import { SpeechToText } from "../SpeechToText/SpeechToText";
import { TextToSpeech } from "../TextToSpeech/TextToSpeech";

// mui-components
import { Box, Typography } from "@mui/material";

// constants
import { SPEECH_TO_TEXT, WHITE } from '../../constants/constants';

// icons
import SpeechBubble from '../../icons/speech-bubble.png';

// mui-styled
import styled from "@emotion/styled";

const MainBox = styled(Box)({
  position: 'relative',
  width: 'calc(100% - 20px)',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-start',
  padding: '10px',
  flexDirection: 'column',
  color: WHITE,
});

const ImageBox = styled(Box)({
  display: 'flex', 
  flexDirection: 'column', 
  justifyContent: 'center', 
  alignItems: 'center'
});

const Icon = styled('img')({
  width: '250px',
  '@media screen and (max-width: 1024px)': {
    width: '210px'
  },
  '@media screen and (max-width: 768px)': {
    width: '170px'
  },
  '@media screen and (max-height: 750px) and (min-width: 769px)': {
    width: '170px'
  },
});

const MainTitle = styled(Typography)({
  fontSize: '46px', 
  color: WHITE, 
  position: 'relative', 
  top: '-160px', 
  textShadow: '3px 2px 1px black', 
  transform: 'rotate(-30deg)',
  '@media screen and (max-width: 1024px)': {
    fontSize: '38px',
    top: '-135px'
  },
  '@media screen and (max-width: 768px)': {
    fontSize: '32px',
    top: '-110px'
  },
  '@media screen and (max-height: 750px) and (min-width: 769px)': {
    fontSize: '32px',
    top: '-110px'
  },
});

export const Dictaphone = () => {

    const [lang, setLang] = useState('ru-RU');
    const [mode, setMode] = useState(SPEECH_TO_TEXT);

    return (
        <MainBox>
          <ImageBox>
            <Icon src={SpeechBubble} />
            <MainTitle component='h1' variant='p'>{mode}</MainTitle>
          </ImageBox>
          {
            mode === SPEECH_TO_TEXT ? (
              <SpeechToText lang={lang} setLang={setLang} setMode={setMode} />
            ) : (
              <TextToSpeech setMode={setMode} />
            )
          }
        </MainBox>
    );
};