// components
import { SelectVoice } from "../SelectVoice/SelectVoice";

// mui-components
import { Box, Button, Tooltip } from "@mui/material";

// mui-icons
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';
import ClearIcon from '@mui/icons-material/Clear';

// constants
import { BTN_COLOR, BTN_HOVER_COLOR, SPEECH_BUBBLE_DARK_SIDE, SPEECH_TO_TEXT } from "../../constants/constants";

// mui-styled
import styled from "@emotion/styled";

const SpeakSettingsBox = styled(Box)({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  flexDirection: 'row',
  flexWrap: 'wrap',
  marginBottom: '15px',
  '@media screen and (max-width: 768px)': {
    marginBottom: '5px',
  }
});

const GetSpeakBtn = styled(Button)({
  margin: '15px',
  width: '64px',
  height: '64px',
  borderRadius: '40px',
  backgroundColor: BTN_COLOR,
  ':hover': {
    backgroundColor: BTN_HOVER_COLOR,
  },
  '@media screen and (max-width: 1024px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
  '@media screen and (max-height: 750px) and (min-width: 1025px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
});

const SetModeBtn = styled(Button)({
  margin: '5px',
  width: '64px',
  height: '64px',
  borderRadius: '40px',
  backgroundColor: BTN_COLOR,
  ':hover': {
    backgroundColor: BTN_HOVER_COLOR,
  },
  '@media screen and (max-width: 1024px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
  '@media screen and (max-height: 750px) and (min-width: 1025px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
});

const CancelSpeakingBtn = styled(Button)({
  width: '64px',
  height: '64px',
  borderRadius: '40px',
  backgroundColor: BTN_COLOR,
  ':hover': {
    backgroundColor: BTN_HOVER_COLOR,
  },
  '@media screen and (max-width: 1024px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
  '@media screen and (max-height: 750px) and (min-width: 1025px)': {
    width: '48px',
    height: '48px',
    borderRadius: '30px',
  },
});

const iconStyles = {
  fontSize: '32px',
  color: SPEECH_BUBBLE_DARK_SIDE,
  '@media screen and (max-width: 1024px)': {
    fontSize: '24px'
  },
  '@media screen and (max-height: 750px) and (min-width: 1025px)': {
    fontSize: '24px'
  },
};

export const TextToSpeechUtils = ({setMode, speaking, speak, value, voices, voice, setVoice, cancel, rate, pitch}) => {

    const onSetModeBtnClicked = () => {
      const app = document.querySelector('.App');
      
      app.style.transform = 'rotateY(90deg)';

      setTimeout(() => setMode(SPEECH_TO_TEXT), 210);
      setTimeout(() => app.style.transform = 'rotateY(0deg)', 230);
    };

    const onGetSpeakBtnClicked = () => {
      if (speaking) return;
      const neededVoice = voices.find(v => v.name === voice);

      try {
        speak({ text: value, voice: neededVoice, rate, pitch });
      } catch {}

    };

    const onCancelSpeakingBtnClicked = () => {
      if (speaking) {

        try {
          cancel();
        } catch {}

      };
    };

    return <SpeakSettingsBox>
    <SelectVoice voices={voices} voice={voice} setVoice={setVoice} cancel={cancel} />
    <Tooltip title='Listen'>
      <GetSpeakBtn onClick={onGetSpeakBtnClicked}>
        <VolumeUpIcon sx={iconStyles} />
      </GetSpeakBtn>
    </Tooltip>
    <Tooltip title='Cancel'>
      <CancelSpeakingBtn onClick={onCancelSpeakingBtnClicked}>
        <ClearIcon sx={iconStyles} />
      </CancelSpeakingBtn>
    </Tooltip>
    <Tooltip title={SPEECH_TO_TEXT}>
      <SetModeBtn onClick={onSetModeBtnClicked}>
        <CompareArrowsIcon sx={iconStyles} />
      </SetModeBtn>
    </Tooltip>
</SpeakSettingsBox>
};