// mui-components & hooks
import { Box, FormControl, MenuItem, Select, useMediaQuery } from "@mui/material";

// mui-icons
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';

// constants
import { langs, SPEECH_BUBBLE_DARK_SIDE, SPEECH_BUBBLE_LIGHT_SIDE } from "../../constants/constants";

// uuid
import { v4 as uuidv4 } from 'uuid';

export const SelectLang = ({lang, setLang, stop}) => {
    const onSelectChange = e => {
      try {
        stop();
      } catch {}

      setLang(e.target.value);
    };

    const is1024px = useMediaQuery('@media screen and (max-width: 1024px)');
    const is500px = useMediaQuery('@media screen and (max-width: 500px)');
    const is750pxHeight = useMediaQuery('@media screen and (max-height: 750px) and (min-width: 1025px)');

    const selectStyles = {
        color: SPEECH_BUBBLE_DARK_SIDE, 
        '.MuiOutlinedInput-notchedOutline': { 
          borderColor: SPEECH_BUBBLE_LIGHT_SIDE,
          transition: 'border 200ms ease',
        },
        '.MuiSelect-select': {
          padding: is500px ? '8px 16px 8px 6px !important' : is1024px || is750pxHeight ? '12px 24px 12px 10px !important' : '16px 32px 16px 14px !important',
          fontSize: is500px ? '12px !important' : is1024px || is750pxHeight ? '14px !important' : '16px !important',
        },
      };

    const iconStyles = {
      color: SPEECH_BUBBLE_DARK_SIDE, 
      pointerEvents: 'none',
       userSelect: 'none', 
       position: 'absolute', 
       right: 12,
       fontSize: is1024px || is750pxHeight ? '20px' : '24px',
    };

    const mainBoxStyles = {
      width: is500px ? '200px' : is1024px || is750pxHeight ? '240px' : '280px',
    };

    const menuItemStyles = {
      fontSize: is500px ? '12px !important' : is1024px || is750pxHeight ? '14px !important' : '16px !important',
    };

    const Icon = () => <KeyboardDoubleArrowDownIcon sx={iconStyles} />

    return <Box sx={mainBoxStyles}>
            <FormControl fullWidth>
                <Select
                    IconComponent={Icon}
                    value={lang}
                    onChange={onSelectChange}
                    sx={selectStyles}
                >   
                  {
                    langs.map(({code, which}) => <MenuItem key={ uuidv4() } sx={menuItemStyles} value={code}>{which}</MenuItem>)
                  }
                </Select>
            </FormControl>
        </Box>
};